*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

html {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*, :before, :after {
  box-sizing: inherit;
}

body {
  font-family: var(--major-font-family);
  color: var(--primary-color);
  letter-spacing: .08em;
  min-height: 100vh;
  position: relative;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

.container-main {
  display: contents;
}

.container {
  width: 100vw;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 320px) {
  .container {
    width: 320px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

:root {
  --major-font-family: "Roboto", sans-serif;
  --modal-fond-family: "Fira Sans Extra Condensed", sans-serif;
  --modal-title-style: "Fjalla One", sans-serif;
  --primary-color: #000;
  --secondary-text-color: #545454;
  --dark-theme-text-color: #fff;
  --accent-color: #ff6b08;
  --accent-text-color: #fff;
  --background-accent-color: #ff6b01;
  --style-color: #ff001b;
  --background-color_bright-theme: #f7f7f7;
  --secondary-text-modal-color: #8c8c8c;
  --background-color_modal-footer: #0e0e0e;
  --duration: 300ms;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

.visually-hidden {
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body.overflow {
  overflow: hidden;
}

.backdrop {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  transition: opacity 500ms var(--timing-function), visibility 500ms var(--timing-function);
  background-color: rgba(0, 0, 0, .7);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)scale(.001);
}

.modal {
  background-color: var(--dark-theme-text-color);
  transition: transform 500ms var(--timing-function);
  width: 280px;
  height: 940px;
  max-height: 560px;
  border-radius: 10px;
  margin: 20px 10px;
  padding: 40px 20px;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow-x: hidden;
  overflow-y: scroll;
  transform: translate(-50%, -50%)scale(1);
}

.modal__close-btn {
  fill: var(--primary-black-color);
  width: 30px;
  height: 30px;
  cursor: pointer;
  transition: background-color 250ms var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  background-image: url("close-btn.d349cae7.svg");
  background-size: contain;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-bottom: 8px;
  padding: 0;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal__close-btn:focus, .modal__close-btn:hover {
  background-color: var(--accent-color);
}

.modal__img {
  margin-top: 8px;
  margin-bottom: 20px;
}

.modal__title {
  color: var(--primary-color);
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.15;
}

.modal__list {
  width: 100%;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  display: flex;
}

.modal__list-item {
  display: flex;
}

.modal__list-description {
  width: 35%;
  height: 100%;
}

.modal__list-api {
  vertical-align: top;
  width: 65%;
  height: 100%;
}

.modal__list-description-item:not(:last-child) {
  margin-bottom: 8px;
}

.modal__list-item-title {
  color: var(--secondary-text-color);
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
}

.modal__list-item-api-value:not(:last-child) {
  margin-bottom: 8px;
}

.modal__list-item-value-vote {
  border: 1px solid var(--accent-color);
  background-color: var(--accent-color);
  color: var(--dark-theme-text-color);
  width: 36px;
  height: 16px;
  border-radius: 5px;
}

.modal__list-item-value {
  text-align: center;
  text-transform: uppercase;
  color: var(--primary-color);
  align-items: center;
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.16667;
  display: flex;
}

.modal__list-item-value-original-title, .modal__list-item-value-genre {
  color: var(--primary-color);
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
  display: flex;
}

.modal__description-title {
  text-transform: uppercase;
  color: var(--primary-color);
  align-items: center;
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
  display: flex;
}

.modal__description-overview {
  color: var(--primary-color);
  margin-bottom: 20px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.66667;
}

.modal__btn-wrapper {
  justify-content: space-between;
  display: flex;
}

.modal__btn {
  width: 110px;
  height: 44px;
  border: 1px solid var(--primary-color);
  background-color: var(--dark-theme-text-color);
  text-transform: uppercase;
  color: var(--primary-black-color);
  cursor: pointer;
  transition: background-color 250ms var(--timing-function), color 250ms var(--timing-function);
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
  display: flex;
}

.modal__btn:hover, .modal__btn:focus {
  border-color: var(--accent-color);
}

.btn-remove {
  background-color: var(--accent-color);
  color: var(--dark-theme-text-color);
  border: none;
}

@media screen and (min-width: 320px) {
  .modal__img {
    width: 240px;
    height: 358px;
  }
}

@media screen and (min-width: 768px) {
  .modal {
    width: 704px;
    height: 552px;
    margin: 0;
    padding: 40px;
    display: flex;
  }

  .modal__img {
    width: 264px;
    height: 374px;
    margin-top: 0;
    margin-right: 68px;
  }

  .modal__overview-wrapper {
    flex-direction: column;
    align-items: flex-start;
    display: flex;
  }

  .modal__title, .modal__list {
    margin-bottom: 20px;
  }

  .modal__list-description {
    margin-right: 40px;
  }

  .modal__description-wrapper {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 20px;
    display: flex;
  }

  .modal__description-overwiew {
    margin-bottom: 0;
  }

  .modal__btn {
    width: 124px;
    margin-bottom: 40px;
    margin-right: 14px;
  }

  .modal__btn:not(:last-child) {
    margin-right: 15px;
  }
}

@media screen and (min-width: 1280px) {
  .modal {
    width: 806px;
    height: 558px;
    padding: 40px 12px;
  }

  .modal__img {
    width: 375px;
    height: 478px;
    margin-bottom: 0;
    margin-right: 16px;
  }

  .modal__title {
    margin-bottom: 20px;
    font-size: 30px;
    font-weight: 500;
    line-height: 1.16667;
  }

  .modal__list-description {
    padding-right: 50px;
  }

  .modal__btn {
    width: 136px;
    justify-content: center;
  }
}

