.mask {
  z-index: 100;
  flex-direction: column;
  align-items: center;
  transition: all .5s;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader {
  width: 75px;
  height: 75px;
  border: 10px solid rgba(250, 180, 75, .6);
  border-left-color: orange;
  border-radius: 50%;
  animation: loader 1.3s linear infinite;
}

@keyframes loader {
  100% {
    transform: rotate(360deg);
  }
}

.hide-load {
  display: none;
}

.footerModal {
  background-color: var(--background-color_modal-footer);
  z-index: 99;
  width: 100%;
  height: 100%;
  transition: opacity 500ms var(--timing-function), visibility 500ms var(--timing-function);
  background-size: contain;
  flex-direction: column;
  justify-content: center;
